@import 'main';
// End Of Import

// Mob Nav
.menuTriger {
    width: 40px;
    height: 40px;
    background-color: rgba(#128c8d, 0.8);
    position: fixed;
    top: 25px;
    right: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 99;
    display: none;

    @include Sm{
        display: flex;
    }

    i{
        color: #ffffff;
        font-size: 20px;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 9;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: -261px;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 99;
    overflow-y: auto;
    transition: .5s;

    &.open{
        right: 0;
    }

    .options {
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .lang a {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #888;
        }

        .close1 {
            display: flex;
            font-size: 20px;
            color: #dedede;
            cursor: pointer;
        }
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 100%;
        // overflow: auto;
        padding: 0px 25px 0;

        & > li{
            position: relative;
            padding: 7px 15px;
            width: 100%;

            &:not(:last-child) {
                border-bottom: 1px solid #dedede;
            }

            a {
                width: 100%;
                display: block;
                font-size: 16px;
                color: $color-prim;
                transition: .5s;

                &:hover{
                    color: $color-sec;
                }
            }

            i{
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: .5s;

                &.open{
                    transform: rotate(0deg);
                }
            }

            .drop{
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li{
                    padding: 4px 0;

                    &:not(:last-child){
                        border-bottom: 1px solid #dedede;
                    }

                    a{
                        font-size: 15px;
                        color: #898989;
                        transition: .5s;
                        
                        &:hover{
                            color: #595959;
                        }
                    }
                }
            }
        }
    }
}

// Mob Nav

// Head

.up-header{
    padding: 15px 0;

    .container{
        position: relative;
    }

    .lang{
        position: absolute;
        top: 0px;
        left: 15px;
        font-size: 13px;
        font-family: "CrSemiBold";
        color: #bfbfbf;
        transition: .5s;

        @include Sm{
            display: none;
        }

        &:hover{
            color: #dadada;
        }

        i{
            margin-right: 7px;
            font-size: 13px;
            color: currentColor;
        }
    }

    .content{
        display: flex;
        align-items: center;

        @include Sm{
            flex-direction: column;
        }

        .search-overlay{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba($color-prim, 0.95);
            z-index: 99;
            opacity: 0;
            visibility: hidden;
            transition: .5s;

            &.open{
                opacity: 1;
                visibility: visible;
            }

            i{
                position: absolute;
                top: 15%;
                left: 50%;
                transform: translateX(-50%);
                font-size: 20px;
                color: rgb(224, 224, 224);
            }
        }

        .search{
            margin-right: 72px;
            width: 553px;
            position: relative;
            transition: .5s;

            @include Sm{
                order: 3;
                margin: auto;
                position: absolute;
                bottom: 10%;
                opacity: 0;
                visibility: hidden;
                z-index: 999;
                width: 70%;
            }

            @include mob480{
                width: 90%;
            }

            &.open{
                opacity: 1;
                visibility: visible;
                bottom: -10%;
            }

            @include Md{
                width: 453px;
            }

            @include Lg{
                width: 553px;
            }

            input{
                width: 100%;
                height: 45px;
                border: 2px solid #f2f2f2;
                padding: 0 20px;
                color: #bbbbbb;
                font-size: 14px;
                transition: .5s;

                &::placeholder{
                    color: #bbbbbb;
                    font-size: 14px;
                }

                &:focus{
                    border: 2px solid $color-sec;
                }
            }

            button{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 6px;
                width: 31px;
                height: 31px;
                background-color: $color-sec;
                border: 0;
                color: #fff;
                font-size: 14px;

                &:after{
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: $color-prim;
                    z-index: -1;
                    transition: .5s;
                    transform: scaleX(0);
                }

                i{
                    backface-visibility: hidden;
                }

                &:hover{

                    &:after{
                        transform: scaleX(1);
                    }
                }
            }
        }

        .options{
            display: flex;
            align-items: center;
            margin-right: auto;

            @include Sm{
                margin: auto;
                margin-top: 20px;
            }

            .option{
                width: 40px;
                height: 40px;
                background-color: #128c8d;
                color: #fff;
                display: flex;
                align-items: center;
                justify-content: center;
                position: relative;
                cursor: pointer;

                &:hover{
                    

                    .drop{
                        transform: translateX(-34%) translateY(5px);
                        opacity: 1;
                        visibility: visible;
                    }
                }

                &:after{
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: $color-sec;
                    z-index: 0;
                    transition: .5s;
                    transform: scaleX(0);
                }

                i{
                    position: relative;
                    z-index: 1;
                }

                &:not(:first-child) {
                    margin-right: 10px;
                }

                &:hover{

                    &:after{
                        transform: scaleX(1);
                    }
                }

                .drop{
                    position: absolute;
                    top: 100%;
                    left: 0;
                    min-width: 132px;
                    min-height: 75px;
                    transform: translateX(-34%) translateY(15px);
                    opacity: 0;
                    visibility: hidden;
                    transition: .5s;
                    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
                    z-index: 9;
                    background-color: #fff;

                    &:after{
                        content: "";
                        width: 0;
                        height: 0;
                        display: block;
                        border-width: 14px;
                        border-color: transparent transparent #ffffff;
                        border-style: solid;
                        top: -19px;
                        position: absolute;
                        left: 50%;
                        transform: translateX(-50%);
                    }

                    ul{
                        padding: 10px 12px;

                        li{
                            padding: 0 12px;
                            text-align: center;

                            &:not(:last-child){
                                border-bottom: 1px solid #f2f2f2;
                            }

                            a{
                                color: $color-prim;
                                transition: 0.5s;
                                font-size: 14px;
                                line-height: 2;

                                &:hover{
                                    color: $color-sec;
                                }
                            }
                        }
                    }
                }

                &.search-mob{
                    display: none;

                    @include Sm{
                        display: flex;
                    }
                }
            }
        }
    }
}

// Head

// Navbar

.navBar{
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #128c8d;

    @include Sm{
        display: none;
    }

    & > ul{
        display: flex;
        align-items: center;
        justify-content: center;

        & > li{
            position: relative;

            i{
                font-size: 8px;
                margin-right: 5px;
            }

            &:not(:last-child){
                margin-left: 25px;
            }

            &:hover{
                .drop{
                    visibility: visible;
                    opacity: 1;
                    transform: translateY(0px) translateX(50%);
                }
            }

            .drop{
                position: absolute;
                top: 95%;
                right: 50%;
                width: 136px;
                min-height: 75px;
                transform: translateY(10px) translateX(40%);
                background-color: rgb(255, 255, 255);
                text-align: center;
                box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
                opacity: 0;
                visibility: hidden;
                z-index: 9;
                transition: .6s cubic-bezier(0.175, 0.885, 0.32, 1.275);

                &:after{
                    content: "";
                    width: 0;
                    height: 0;
                    display: block;
                    border-width: 14px;
                    border-color: transparent transparent #ffffff;
                    border-style: solid;
                    top: -19px;
                    position: absolute;
                    right: 50%;
                    transform: translateX(50%);
                    z-index: 5;
                }

                ul{
                    padding: 12px 13px;

                    li{

                        a{
                            color: #515151;
                            transition: .5s;
                            font-size: 14px;
                            line-height: 2;

                            &:hover{
                                color: #128c8d;
                            }
                        }

                        &:not(:last-child){
                            border-bottom: 1px solid #f2f2f2;
                        }

                        a{
                            transition: .5s;
                        }
                    }
                }
            }

            a{
                font-size: 16px;
                line-height: 2.752;
                font-family: "CrSemiBold";
                color: $color-white;
                position: relative;
                z-index: 9;
                transition: .5s;
                
                &:hover{
                    color: $color-sec;
                }
            }
        }
    }
}

// Navbar

.owl-nav{
    position: absolute;
    top: 49%;
    transform: translate(50%,-50%);
    right: 50%;
    width: 100%;
    height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include Sm{
        display: none;
    }

    button{

        &.owl-prev{
            span{
                border-left: 0;
                border-bottom: 0;
            }
        }

        &.owl-next{
            span{
                border-right: 0;
                border-top: 0;
            }
        }
    }
}

// Slider Styles

.owl-dots{
    position: absolute;
    bottom: -10px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    
    button{

        &:not(:last-child){
            margin-left: 8px;
        }

        &.active{

            span{
                background-color: $color-sec;
            }
        }

        span{
            display: block;
            width: 8px;
            height: 8px;
            background-color: #767778;
        }
    }
}

// Main Slider

.slider{
    margin: 17px 0;

    .main-slider{
        overflow: hidden;

        @include Sm{
            margin-bottom: 20px;
        }
        
        .owl-nav{
            position: absolute;
            top: 49%;
            transform: translate(50%,-50%);
            right: 50%;
            width: 111%;
            height: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            transition: .5s;
    
            @include Sm{
                display: none;
            }
    
            button{
                width: 40px;
                height: 40px;
                background-color: rgba($color-prim, 0.6) !important;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: .5s;
    
                &:hover{
                    background-color: $color-sec !important;
                }
    
                &.owl-prev{
                    span{
                        border-left: 0;
                        border-bottom: 0;
                        transform: rotate(45deg) translate(-3px, 3px);
                    }
                }
    
                &.owl-next{
                    transform: translateX(-0.4px);
                    span{
                        border-right: 0;
                        border-top: 0;
                        transform: rotate(45deg) translate(3px, -3px);
                    }
                }
    
                span{
                    display: block;
                    width: 15px;
                    height: 15px;
                    border: 1px solid #fff;
                    transform: rotate(45deg);
                }
            }
        }

        .owl-dots {
            bottom: 30px;
            
            @include Sm{
                bottom: 20px;
            }

            @include Xs{
                bottom: 10px;
            }
        }
    
        &:hover{
    
            .owl-nav{
                width: 100%;
            }
        }
    }

    .categories{

        @include Md{
            margin-top: 15px;
        }

        @include Lg{
            margin-top: 0;
        }

        .col-xs-12{

            &:first-child{
                margin-bottom: 15px;

                @include Md{
                    margin-bottom: 0px;
                }

                @include Lg{
                    margin-bottom: 15px;
                }
            }
        }

        .category{
            position: relative;
            overflow: hidden;

            @include Md{
               width: max-content;
               margin: auto;
            }
    
            @include Lg{
                width: 100%;
                margin: unset;
            }

            @include Xs{
                width: max-content;
                margin: auto;
            }

            @include mob375{
                width: 100%;
                margin: unset;
            }

            &:hover{
                img{
                    transform: rotate(3deg) scale(1.09);                }
            }

            img{
                transition: .8s cubic-bezier(0.86, 0, 0.07, 1);

                @include Md{
                    margin: auto;
                    display: block;
                }
        
                @include Lg{
                    margin: unset;
                }
            }

            .title{
                background-color: rgba(18, 140, 141, 0.9);
                font-family: "CrBold";
                font-size: 16px;
                color: $color-white;
                padding: 8px 15px;
                position: absolute;
                bottom: 30px;
                right: 0;
                margin: 0;
            }
        }
    }
}

// Main Slider

.bestSeller,.featured,.recent-arrived{

    .title{
        font-family: "CrBold";
        font-size: 17px;
        color: $color-prim;
        height: 48px;
        background-color: #f7f7f7;
        position: relative;
        padding: 0 25px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        // margin-bottom: 5px;


        &:after{
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            height: 100%;
            width: 3px;
            background-color: #128c8d;
        }

        p{
            margin: 0;
        }

        a{
            font-size: 12px;
            color: #bfbebe;
            transition: .5s;

            &:hover{
                color: $color-sec;
            }
        }
    }

    .disc.custom{
        position: relative;
        z-index: 9;
    
        .disc-head{
            position: relative;
            display: flex;
            align-items: center;
            justify-content: space-between;
    
            @include Xs{
                width: 94%;
                margin: 37px auto 37px;
            }
        
            & > p{
                font-size: 22px;
                font-family: "frtBold";
                color: $color-sec;
                margin-bottom: 0;
                margin-right: 5px;
    
                @include Sm{
                    margin-right: 0;
                }
    
                @include mob375{
                    font-size: 20px;
                }
            }
        
            & > a{
                font-family: "frtBold";
                color: #a5a5a5;
                transition: .5s;
                font-size: 14px;
                color: rgb(207, 207, 207);
                margin-left: 5px;
    
                @include Md{
                    margin-left: 20px;
                }
    
                @include Lg{
                    margin-left: 0px;
                }
    
                &:hover{
                    color: $color-prim;
                }
            }
        }
        
        .owl-carousel{
    
            .owl-stage{
                padding: 10px 0;
            }
    
            .owl-item{
                display: flex;
                justify-content: center;
            }
    
            .owl-nav{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 105%;
                height: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
                font-size: 30px;
                margin-top: 0;
                color: #c4c4c4;
                padding: 0;
                right: unset;
    
                @include Md{
                    width: 100%;
                }
    
                @include Lg{
                    width: 105%;
                    top: 50%;
                }
    
                @include Sm{
                    display: none;
                }
    
                button{
                    width: 15px;
                    height: 15px;
                    border: 2px solid #d0d0d0;
                    border-right: none;
                    border-bottom: none;
                    transform: rotate(-45deg);
                    border-radius: 1px;
                    transition: .5s;
    
                    &.owl-prev{
                        transform: rotate(135deg);
                    }
    
                    &:hover{
                        background-color: transparent;
                        border: 2px solid $color-sec;
                        border-right: none;
                        border-bottom: none;
                    }
    
                    span{
                        display: none;
                    }
                }
            }
    
            .owl-dots{
                display: none;
    
                @include Sm{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: unset;
                    transform: translateY(20px);
                    margin-bottom: 15px;
                }
            }
    
            .owl-item{
    
                img{
                    width: initial;
                    margin: auto;
                }
            }
        }
    }
}

.product{
    text-align: center;
    width: 180px;
    background-color: transparent;
    border: 1px solid #e9e9e9;
    padding-bottom: 20px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    transition: .5s;

    &:hover{

        .pic{

            img{
                animation: flash 1.5s;
            }
        }
    }

    .pic{
        height: 146px;
        width: 100%;
        overflow: hidden;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 15px;
        transition: .5s;

        &:hover{
            &:after{
                animation: shape .5s linear;
            }
        }

        &:after{
            content: "";
            position: absolute;
            top: -103%;
            left: -100%;
            width: 100%;
            height: 170%;
            transition: .7s ease;
            background-image: linear-gradient(to right, transparent, rgba($color-white, 0.3));
            transform: rotate(45deg);
        }

        a{
            display: flex;
            width: 100%;
            height: 100%;
        }
    }

    .detail{
        padding: 15px 0 0px 0;
        background-color: transparent;
    }

    & > a{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    & > img{
        width: initial;
        height: 167px;
        display: block;
        margin: auto;
    }

    .pro-title{
        font-size: 14px;
        font-family: "Cairo";
        color: $color-prim;
        transition: .5s;

        &:hover{
            color: $color-prim;
        }
    }

    span{
        font-size: 15px;
        font-family: "CrBold";
        display: inline-block;
        direction: ltr;

        &.price{
            color: #128c8d;
            margin-right: 8px;
        }

        &.discount{
            color: #919292;
            text-decoration: line-through;
            font-family: "Cairo";
        }
    }
}

// Main Slider

// Banner

.banner{
    margin: 5px 0 15px;

    @include Sm{
        margin: 30px 0 15px;
    }

    a{
        display: block;
        overflow: hidden;
    }

    img{
        width: 100%;
        transition: .5s;

        &:hover{

            transform: scale(1.09);
        }
    }
}

// Banner

.recent-arrived{
    margin-top: 10px;

    @include Sm{
        margin-top: 30px;
    }
}

// Clients

.clients{
    margin-top: 50px;


    .owl-carousel .owl-item img{
        width: initial;
    }


    .item{


        a{
            display: flex;
            width: 108px;
            height: 108px;
            align-items: center;
            justify-content: center;
            margin: auto;

            img{
                opacity: .5;
                filter: grayscale(100%);
                transition: .5s;
            }

            &:hover{

                img{
                    opacity: 1;
                    filter: none;
                }
            }
        }
    }

    .owl-dots{
        @include Md{
            display: none;
        }
    }
}

// Clients

// footer

footer{
    margin-top: 10px;
    padding: 58px 0;
    padding-bottom: 0;
    background-color: #128c8d;

    @include Sm{
        padding: 40px 0;
        margin-top: 40px;
    }

    .foot-links{

        @include Lg{
            text-align: right;
        }

        @include Sm{
            margin-bottom: 15px;
        }

        .collapse{
            display: block;

            @include Sm{
                display: none;
            }

            &.in{
                display: block;
            }
        }

        & > button{
            font-size: 17px;
            font-family: "CrBold";
            color: #fff;
            margin-bottom: 20px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans{

                &:after{
                    content: "\2212";
                }
            }

            @include Sm{
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480{
                width: 100%;
            }

            &:after{
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }

        ul{

            @include Sm{
                padding-top: 10px;
                padding-right: 10px;
            }

            li{
                &:not(:last-child){
                    margin-bottom: 10px;
                }
            }
        }

        a{
            font-size: 15px;
            font-family: "Cairo";
            color: #83e1e2;
            transition: .5s;

            &:hover{
                color: $color-white;
            }
        }
    }

    .newsletter{
        
        @include Md{
            margin-top: 35px;
            text-align: center;
        }

        @include Lg{
            margin-top: 0;
            text-align: right;
        }

        @include Sm{
            margin-top: 35px;
            text-align: center;
        }

        & > p{
            font-size: 16px;
            font-family: "CrBold";
            color: #fff;
            margin-bottom: 20px;
        }

        & > span{
            font-size: 14px;
            font-family: "Cairo";
            color: #fff;
            margin-bottom: 15px;
            display: block;
        }
    }

    .frm{
        position: relative;
        width: 360px;
        
        @include Md{
            margin: auto;
            margin-bottom: 20px;
        }

        @include Sm{
            margin: auto;
            margin-bottom: 20px;
        }

        @include mob375{
            width: 280px;
        }

        input{
            padding: 0 20px;
            height: 45px;
            width: 100%;
            border: 0;
            color: #aeaeae;
            font-size: 14px;
            background-color: $color-white;
            transition: .5s;

            &::placeholder{
                color: #aeaeae;
            }
        }

        button{
            position: absolute;
            top: 50%;
            left: 7px;
            transform: translateY(-50%);
            width: 30px;
            height: 30px;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $color-sec;
            color: $color-white;
            overflow: hidden;
            transition: .5s;



            i{
                position: relative;
                z-index: 1;
            }

            &:after{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                background-color: $color-prim;
                z-index: 0;
                width: 100%;
                height: 100%;
                transform: scaleX(0);
                transition: .5s;
            }

            &:hover{
                &:after{
                    transform: scaleX(1);
                }
            }
        }
    }

    .social{


        a{

            i{
                font-size: 18px;
                color: #83e1e2;
                transition: .5s;
            }

            &:not(:last-child){
                margin-left: 20px;
            }

            &:hover{

                i{
                    color: #FFF;
                }
            }
        }
    }
}

// End Of Footer

