@keyframes shape{
    from{
        opacity: 0;
        top: -103%;
        left: -100%;
    }to{
        top: 50%;
        left: 65%;
    }
}

@keyframes pulse{
    0%{
      transform: scale(1);
    }
    
    50%{
      transform: scale(1.1);
    }
    
    100%{
      transform: scale(1);
    }
  }